.landding-2 {
	.title {
		width: 50%;
		padding-bottom: 70px;
		margin: 0 auto;
		h1 {
			font-size: 63px;

			color: rgb(39, 96, 173);
			font-style: italic;
			font-weight: 700;
			line-height: 1;
			text-align: center; }
		@media screen and ( max-width: 1024px) {
			width: 100%;
			padding-bottom: 30px;
			h1 {
				font-size: 30px; } } }

	.content {
		width: 100%;
		border-top: solid 1px #adadad;
		border-bottom: solid 1px #adadad;
		padding-top: 50px;
		padding-left: 70px;
		padding-right: 0;
		.desc {
			padding-bottom: 50px;
			padding-right: 63px;
			p {
				font-size: 20.73px;

				color: rgb(0, 0, 0);
				line-height: 1.2;
				text-align: left; } }
		.left {
			border-right: solid 1px #adadad;
			height: 100%;
			width: 100%;
