				height: 461px;
				figure {
					.img {
						width: 100%;
						height: 324px;
						padding-bottom: 42px;
						img {
							width: 100%;
							height: 100%; }
						@media screen and ( max-width: 557px) {
							height: auto; } }
					figcaption {
						text-align: center;
						padding: 0 35px;
						p {
							font-size: 20.73px;

							color: rgb(0, 0, 0);
							line-height: 24.18px;
							padding-bottom: 42px; }
						a {
							position: relative;
							font-size: 16px;

							color: rgb(0, 0, 0);
							line-height: 2;
							text-align: center;
							&::after {
								position: absolute;
								content: '';
								bottom: -10px;
								left: 0;
								width: 100%;
								height: 2px;
								background-color: #adadad; } } } } }
			&:last-child {
				border: none; } }
		@media screen and ( max-width: 1024px) {
			padding: 15px;
			padding-bottom: 0;
			.desc {
				padding-bottom: 20px;
				padding-right: 0; }
			.left {
				.item {
					height: 420px;
					figure {
						.img {
							padding-bottom: 15px; }
						figcaption {
							padding: 15px;
							p {
								padding-bottom: 0; } } } } } }
		@media screen and ( max-width: 557px) {
			.left {
				.item {
					height: auto; } } } }
	.big-img {
		width: 100%;
		padding-left: 0;
		@media screen and ( max-width: 1024px) {
			padding-left: 15px; }
		.big-img {
			width: 100%;
			.img {
				width: 100%;
				img {
					width: 100%; } } }
		@media screen and ( max-width: 575px) {
			padding-left: 15px; } } }
