.landding-5 {
	padding-top: 0;
	.block-title {
		h1 {
			font-size: 69.09px;

			color: rgb(39, 96, 173);
			text-align: center;
			@media screen and ( max-width: 1024px) {
				font-size: 30px; }
			@media screen and ( max-width: 768px) {
				font-size: 25px; } } }
	.section-slide {
		position: relative;
		.item {
			border-width: 2px;
			border-color: transparent;
			border-style: solid;
			border-radius: 20px;
			width: 100%;
			height: 300px;
			padding: 0 50px;
			display: flex;
			flex-direction: column;
			justify-content: center;
			transition: all ease-in-out .3s;
			p {
				font-size: 20.73px;

				color: rgb(98, 98, 98);
				font-style: italic;
				line-height: 1.2;
				text-align: left; }
			h2 {
				font-size: 20.73px;
				font-weight: 700;
				color: #000000;
				padding-top: 50px; }
			&:hover {
				border-width: 2px;
				border-color: rgb(0, 114, 255);
				border-style: solid;
				border-radius: 20px;
				transition: all ease-in-out .3s; }
			@media screen and ( max-width: 1024px) {
				height: auto;
				padding: 15px; }
			@media screen and ( max-width: 768px) {
				h2 {
					padding-top: 10px; } } }
		.swiper-button-next,
		.swiper-button-prev {
			width: 43px;
			height: 43px;
			background-image: none;
			border-radius: 50%;
			background-color: rgba(0, 114, 255, .5); }
		.swiper-button-next {
			right: -35px;
			&::after {
				content: url('../assets/banner/next.png');
				z-index: 99; } }
		.swiper-button-prev {
			left: -35px;
			&::after {
				content: url('../assets/banner/prev.png');
				z-index: 99; } } } }
