.global-breadcrumb {
	width: 100%;
	background-color: #f5f5f5;
	border-bottom: solid 1px rgb(215, 215, 215);
	.breadcrumb {
		display: flex;
		align-items: center;
		background-color: transparent;
		flex-wrap: wrap;
		padding: 10px 0;
		list-style: none;
		margin-bottom: 0;
		li {
			position: relative;
			padding: 0 10px;
			display: flex;
			align-items: center;
			a {
				font-size: 14px;
				color: rgb(102, 102, 102);
				font-weight: 400;
				font-family: Barlow;
				text-decoration: none;
				@media(max-width: 991.98px) {
					font-size: 10px; } }

			&:hover {
				a {
					color: $main-color; } }

			&:first-child {
				padding-left: 0;
				a {
					font-size: 0;
					&::after {
						font-family: 'Material Icons';
						font-size: 16px;
						content: "home"; } } } }

		li+li {
			&::before {
				position: absolute;
				top: 50%;
				left: -7px;
				transform: translateY(-50%);
				font-family: Material Icons;
				font-size: 14px;
				content: "chevron_right";
				color: rgb(102, 102, 102); } } }

	@media(min-width: 768.98px) {
		margin-bottom: 0; }

	@media(min-width: 320px) {
		margin-bottom: 0; } }
